    methods: {
      setActive(assistant) {
        if(assistant === 'assistant1'||assistant === 'assistant2'||assistant === 'assistant3'){
          this.assistant1 = assistant === 'assistant1';
          this.assistant2 = assistant === 'assistant2';
          this.assistant3 = assistant === 'assistant3';
        }
        if(assistant === 'property1'||assistant === 'property2'||assistant === 'property3'||assistant === 'property4'){
          this.property1 = assistant === 'property1';
          this.property2 = assistant === 'property2';
          this.property3 = assistant === 'property3';
          this.property4 = assistant === 'property4';
        }
        if(assistant === 'collaborative1'||assistant === 'collaborative2'||assistant === 'collaborative3'){
          this.collaborative1 = assistant === 'collaborative1';
          this.collaborative2 = assistant === 'collaborative2';
          this.collaborative3 = assistant === 'collaborative3';
        }
      },
      resetIfNoneActive() {
        if (!this.assistant1 && !this.assistant2 && !this.assistant3) {
          this.assistant1 = true;
        }
        if (!this.property1 && !this.property2 && !this.property3&& !this.property4) {
          this.property1 = true;
        }
        if (!this.collaborative1 && !this.collaborative2 && !this.collaborative3) {
          this.collaborative1 = true;
        }
      },
      toggleVersion() {
        this.isExpanded = !this.isExpanded;
      },
      selectColumn(column) {
        this.selectedColumn = this.selectedColumn === column ? null : column;
      },
    }
